            duration: 500,
            delay: 1000,
            rotate: {
                x: 0,
                y: 100,
                z: 0,
            },
        })
        this.scrollReveal.reveal('.home-two__content-4', {
            duration: 500,
            delay: 1500,
            rotate: {
                x: 0,
                y: 100,
                z: 0,
            },
