            delay: 2500,
            rotate: {
                x: 0,
                y: 100,
                z: 0,
            },
        })
        this.scrollReveal.reveal('.home-tow__content', {
            origin: 'bottom',
            distance: '1rem',
            delay: 3000,
        })

        this.scrollReveal.reveal('.home-four__left', {
            origin: 'left',
            distance: '1rem',
        })
        this.scrollReveal.reveal('.home-four__right', {
            origin: 'right',
            distance: '1rem',
        })

        this.scrollReveal.reveal('.home-five__content', {
            origin: 'bottom',
            distance: '1rem',
        })

        

        
        

        
    },
    methods: {
        scrollHandler() {
            this.showNavBg = window.scrollY > 10            
        },
        navClick(item, index) {
            console.log(index)
            switch (index) {
                case 0:
                case 1:
                case 2:
                    ElNotification({
                        title: 'Tips',
                        message: 'Coming Soon',
                        type: 'error',
                    })
                    break;
                case 3: 
                    window.open('https://docs.scoutdoge.com/welcome/')
                    break;
            }
        }
    },
    computed: {

    },
    beforeDestroy() {
        document.removeEventListener('scroll', this.scrollHandler)
    },
}
</script>

